.Home {
    width: 100vw;
    min-height: 100vh;
    margin: 0;
    background-color: white;
    display: flex;
    overflow: auto;
}

.home-zone {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.home-pic-1 {
    width: 100vw;
    height: 100vh;
    background: url("../pictures/toxic_till_the_end.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    flex-direction: column;
    padding: 0 0 40px 0;
}

#home-pic-1-gradient {
    width: 100vw;
    height:30%;
    display: flex;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.76), transparent);
}

#home-album-name-1 {
    width: 550px;
    aspect-ratio: 905 / 275;
    background: url("../pictures/album_logo.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin: 225px auto 0 auto;
    transition: opacity 0.5s ease;
}

#home-album-name-1:hover {
    opacity: 0.75;
    transition: opacity 0.5s ease;
}

#home-album-subtitle {
    width: 100%;
    display: flex;
}

#home-album-subtitle-button {
    width: fit-content;
    font-size: 15px;
    font-weight: bold;
    color: white;
    margin: 35px auto 0 auto;
    font-family: "loos-extended", sans-serif;
    background-color: transparent;
    padding: 12px 35px;
    border: 0.5px solid white;
    border-radius: 40px;
    align-items: center;
    transition: background-color 0.5s ease, color 0.5s ease, border 0.5s ease;
}

#home-album-subtitle-button:hover {
    color: white !important;
    cursor: pointer;
    background-color: #991829;
    border: 0.5px solid #991829;
    transition: background-color 0.5s ease, color 0.5s ease, border 0.5s ease;
}

@media (max-width: 768px) {
    #home-pic-1-gradient {
        width: 100vw;
        height:30%;
        display: flex;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0.9), transparent);
    }
    
    #home-album-name-1 {
        width: 470px;
        aspect-ratio: 905 / 275;
        background: url("../pictures/album_logo.png");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        margin: 225px auto 0 auto;
        transition: opacity 0.5s ease;
    }
    
    #home-album-name-1:hover {
        opacity: 0.75;
        transition: opacity 0.5s ease;
    }
    
    #home-album-subtitle {
        width: 100%;
        display: flex;
    }
    
    #home-album-subtitle-button {
        width: fit-content;
        font-size: 13px;
        font-weight: bold;
        color: white;
        margin: 35px auto 40px auto;
        font-family: "loos-extended", sans-serif;
        background-color: transparent;
        padding: 12px 35px;
        border: 0.5px solid white;
        border-radius: 40px;
        align-items: center;
        transition: background-color 0.5s ease, color 0.5s ease, border 0.5s ease;
    }
    
    #home-album-subtitle-button:hover {
        color: white !important;
        cursor: pointer;
        background-color: #991829;
        border: 0.5px solid #991829;
        transition: background-color 0.5s ease, color 0.5s ease, border 0.5s ease;
    }
}
  